<template>
	<div>
		<div class="detail-wrap">
			<img src="../../../img/7fffffffffffffff5c7f3b542e9b878080.webp" alt=""  width="100%">
			<div>
				<video id="myVideo" class="video-js">
					<source src="/src/video/430c13ff307b4716b5b00b48ed19cbceconv.mp4" type="video/mp4">
				</video>
			</div>
			<img :src="v.pic" alt="" width="100%" v-for="(v,i) in list">
			<div class="marking_price_wrap">
				<p class="marking_price__title"></p>
				<p class="marking_price__content"></p>
				<p class="marking_price__title"></p>
				<p class="marking_price__content"></p>
				<p class="marking_price__content">.</p>
				<p class="marking_price__content"></p>
			</div>
		</div>
	</div>
</template>
<script>
    export default{
    	data(){
    		return{
    			list:[],
    		}
    	},
    	mounted(){
    		var _this=this
			this.initVideo();
    		this.$http.get('./data/detail.json')
    		  .then(function (response) {
    		    // console.log(response);
    			_this.list=response.data.list;
    		  })
    		  .catch(function (error) {
    		    console.log(error);
    		  })
    		  .then(function () {
    		    // always executed
    		})
    	},
    methods: {
        initVideo() {
        //
        let myPlayer = this.$video(myVideo, {
            //autoplayPlayer API
            controls: true,
            //,muted:
            autoplay: "muted",
            //<video>
            preload: "auto",
            //
            width: "100px",
            //
            height: "400px"
        });
        }
    }
    }
</script>
<style>
	.detail-wrap img{
		display: block;
	}
	.marking_price_wrap {
		padding: 17px 25px 20px 25px;
		font-size: 10px;
		background-color: #fff;
	}	
	.marking_price_wrap .marking_price__title {
		position: relative;
		height: 10px;
		line-height: 10px;
		padding-left: 5px;
		margin-bottom: 6px;
		font-weight: bold;
		color: #000;
		vertical-align: baseline;
	}
	.marking_price_wrap .marking_price__content {
		padding-left: 5px;
		margin-bottom: 15px;
		color: #999;
	}
	.marking_price_wrap .marking_price__title::after {
		box-sizing: border-box;
		position: absolute;
		content: '';
		top: -2px;
		left: 0;
		width: 2px;
		height: 10px;
		border-left: 2px solid #000;
	}
	.video-js{
		width: 100%;
		height: 400px;
	}
</style>


